import Inventory from './inventory';

//      
//       ,   ...

export default class Player {
    constructor() {
        //    
        this.inventory = new Inventory();
        //     
        this.characters = [];
        //            
        //     ...        . 
        //     
        this.controlCharacter = null;

        this.tags = [];
    }

    addTag(tag) {
        if (this.tags.indexOf(tag) < 0) {
            this.tags.push(tag);
        }
    }

    hasTag(tag) {
        return this.tags.indexOf(tag) >= 0;
    }
}


